.form-container {
    /* Your existing styles here */
  
    /* Transition for smooth background color and text color change */
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .form-container:hover {
    background-color: white; /* Replace with your desired hover background color */
    color: black; /* Set the text color to the desired color when the form is hovered */
  }
  .form-container:hover h1 {
    color: black; /* Set the text color to the desired color when the form is hovered */
  }

